<template>
  <!-- 考试题目页面 -->
  <div>
    <!-- 最外层div，循环几组题目 -->
    <div
      v-for="(item, index) of list3"
      :key="index"
      style="margin-bottom: 10px"
    >
      <!-- 这是题目 -->
      <p class="title">{{ item.title }}</p>
      <!-- 这是选项 -->
      <van-radio-group v-model="form.radioArray[index]" @change="hand(item,item.id,index)">
        <van-radio
          :name="item.answer1"
          class="box"
        >{{ item.answer1 }}</van-radio
        >
        <van-radio
          :name="item.answer2"
          class="box"
        >{{ item.answer2}}</van-radio
        >
        <van-radio
          :name="item.answer3"
          class="box"
        >{{ item.answer3 }}</van-radio
        >
      </van-radio-group>
    </div>
    <div>
      <van-button icon="edit" type="primary"  @click="getinfo">完成考试</van-button>

    </div>
  </div>
</template>

<script>
  import exam from "../../api/exam";
    export default {
        data() {
            return {
                form: {
                    //单选部分渲染数据
                    radioArray: [],
                },
                //获取选中项对应题目和答案
                value:[],
                //单选项循环数据
                list3:[]
            };
        },
        created() {
            this.getinfo2()
        },
        methods: {
            //单选数据
            hand(item,id,index){
                let aa={id:id,answer:this.form.radioArray[index]}
                this.value.push(aa)
                for (var i = 0; i < this.value.length; i++) {
                    for (var j = i + 1; j < this.value.length; j++) {
                        if (this.value[i].id == this.value[j].id) {
                            this.value.splice(i, 1);
                            j--;
                        }
                    }
                }
            },
            getinfo(){
                //退回主页面
                // this.$router.go(-2)
                exam.getBoardInfo()
            },
            getinfo2(){
                exam.getQuestionnaire().then((resp) => {
                    console.log("ooooooooo"+resp.data.code)
                    if (resp.data.code === 666) {
                        this.list3=resp.data.obj
                    } else {
                        this.$notify({
                            title: 'Tips',
                            message: resp.message,
                            type: 'error',
                            duration: 2000
                        })
                    }
                })


            }
        },
    };
</script>

<style scoped>
  .box {
    height: 50px;
    text-align: center;
    line-height: 50px;
    border-radius: 20px;
    margin-bottom: 2px;
    padding-left: 40px;
    font-size: 16px;
    border-bottom: 1px #ccc solid;

  }
  .title {
    text-align: center;
    line-height: 25px;
    margin-bottom: 2px;
    border: solid #ccc 1px;
    background-color: #1989fa;
    font-size: 16px;
    color: #fff;
  }

</style>
